import { invert } from 'lodash';

const TO_CORVID_ENTITY_RENAMED_KEYS = {
  id: '_id',
  createdDate: '_createdDate',
  updatedDate: '_updatedDate',
};
const FROM_CORVID_ENTITY_RENAMED_KEYS = invert(TO_CORVID_ENTITY_RENAMED_KEYS);

const isReservedCorvidFieldName = (name) =>
  name in FROM_CORVID_ENTITY_RENAMED_KEYS;

const isReservedCommonFieldName = (name) =>
  name in TO_CORVID_ENTITY_RENAMED_KEYS;

const toCorvidName = (name) => TO_CORVID_ENTITY_RENAMED_KEYS[name] || name;

const fromCorvidName = (name) => FROM_CORVID_ENTITY_RENAMED_KEYS[name] || name;

export {
  FROM_CORVID_ENTITY_RENAMED_KEYS,
  fromCorvidName,
  isReservedCommonFieldName,
  isReservedCorvidFieldName,
  TO_CORVID_ENTITY_RENAMED_KEYS,
  toCorvidName,
};
